import React, { useState } from "react";
import {
  TabContent,
  TabPane,
  Nav,
  NavItem,
  NavLink,
  Row,
  Col,
} from "reactstrap";

import CashReceptJournal from "./Journals/CashReceiptJournal";
import GeneralJournal from "./Journals/GeneralJournal";
import PaymentJournal from "./Journals/CashPaymentJournal";

import Page from "../../components/Page";

const FinanceJournals = () => {
  const [activeTab, setActiveTab] = useState("1");

  const toggle = (tab) => {
    if (activeTab !== tab) setActiveTab(tab);
  };

  return (
    <div>
      <Page>
        <Nav tabs className="mb-2">
          <NavItem>
            <NavLink
              className={
                activeTab === "1" ? "activepageTabBlue" : "notactivePageTabBlue"
              }
              onClick={() => {
                toggle("1");
              }}
            >
              General Journal
            </NavLink>
          </NavItem>

          <NavItem>
            <NavLink
              className={
                activeTab === "2" ? "activepageTabBlue" : "notactivePageTabBlue"
              }
              onClick={() => {
                toggle("2");
              }}
            >
              Cash Receipt
            </NavLink>
          </NavItem>

          <NavItem>
            <NavLink
              className={
                activeTab === "3" ? "activepageTabBlue" : "notactivePageTabBlue"
              }
              onClick={() => {
                toggle("3");
              }}
            >
              Cash Payment Journal
            </NavLink>
          </NavItem>
        </Nav>
        <TabContent activeTab={activeTab}>
          <TabPane tabId="1">
            <Row>
              <Col>
                <GeneralJournal />
              </Col>
            </Row>
          </TabPane>
          <TabPane tabId="2">
            <Row>
              <Col>
                <CashReceptJournal />
              </Col>
            </Row>
          </TabPane>
          <TabPane tabId="3">
            <Row>
              <Col>
                <PaymentJournal />
              </Col>
            </Row>
          </TabPane>
        </TabContent>
      </Page>
    </div>
  );
};

export default FinanceJournals;
